import { inspect } from "util";
import Vue from "vue";
export default Vue.extend({
    name: "create-user",
    data: () => {
        let addUserData = {
            step: 1,
            phone: "",
            phoneMessage: "",
            name: "",
            address: "",
            disableButtons: false,
            disableInputs: false,
            loading: false,
            submited: false,
            success: false,
            skipValidation: false,
        };
        return addUserData;
    },
    watch: {
        step: () => {
            window.resizeWindowToCard();
        },
    },
    methods: {
        next() {
            document.getElementById("next").click();
        },
        phoneChange() {
            if (!this.phone)
                return;
            let ph = this.phone;
            this.phoneMessage = "";
            if (!(ph.startsWith("+") || ph.startsWith("0"))) {
                ph = "+91" + ph;
            }
            ph = (ph.startsWith("+") ? "+" : "") + ph.replace(/[^0-9]/g, "").trim();
            if (ph.startsWith("+91") && ph.length > 3) {
                ph = "+91 " + ph.slice(3, 13);
            }
            if (ph.startsWith("0") && ph.length > 5) {
                ph = ph.slice(0, 5) + "-" + ph.slice(5, 11);
            }
            setTimeout(() => {
                this.phone = ph;
            }, 0);
        },
        stepForward() {
            if (this.skipValidation) {
                this.step++;
                return;
            }
            this.disableButtons = true;
            this.disableInputs = true;
            this.loading = true;
            this.validate((success) => {
                if (success)
                    this.step++;
                this.disableInputs = false;
                this.disableButtons = false;
                this.loading = false;
            });
        },
        validate(fn) {
            if (this.step > 3)
                return fn(true);
            switch (this.step) {
                case 1:
                    if (!this.name)
                        return fn(false);
                    return fn(true);
                    break;
                case 2:
                    if (!this.phone)
                        return fn(false);
                    if (this.phone.startsWith("+91") && this.phone.length < 14) {
                        this.phoneMessage = "Indian phone numbers must contain 10 digits";
                        return fn(false);
                    }
                    if (this.phone.startsWith("0") && this.phone.length < 12) {
                        this.phoneMessage = "Local landline numbers must contain 11 digits";
                        return fn(false);
                    }
                    this.phoneMessage = "";
                    window.ipcrenderer.once("phone-exists", (event, err, response) => {
                        if (!response) {
                            fn(true);
                        }
                        else {
                            this.phoneMessage = "Number Already exists";
                            fn(false);
                        }
                    });
                    window.ipcrenderer.send("phone-exists", this.phone);
                    break;
                case 3:
                    if (!this.address)
                        return fn(false);
                    return fn(true);
                    break;
            }
        },
        submit() {
            if (this.submited) {
                window.close();
                return;
            }
            this.submited = true;
            this.disableInputs = true;
            this.skipValidation = true;
            window.ipcrenderer.once("create-user", (event, err, data) => {
                if (err) {
                    window.ipcrenderer.send("show-message-box", {
                        message: "Some error occoured during the creation of User",
                        type: "error",
                        title: "Cannot create User!",
                        detail: inspect(err),
                    });
                    this.success = false;
                }
                else if (data) {
                    window.ipcrenderer.send("show-message-box", {
                        message: "User created SUCCESSFULLY !",
                        type: "info",
                        title: "Created New User!",
                        detail: inspect(data),
                    });
                    this.success = true;
                }
                else {
                    window.ipcrenderer.send("show-message-box", {
                        message: "Some error occoured during the creation of User\nTry checking phone number.",
                        type: "error",
                        title: "Cannot create User!",
                        detail: "err and detail variables are undefined",
                    });
                    this.success = false;
                }
            });
            window.ipcrenderer.send("create-user", {
                name: this.name,
                phone: this.phone,
                address: this.address,
            });
        },
    },
    computed: {
        currentTitle() {
            switch (this.step) {
                case 1:
                    return "Add User";
                case 2:
                    return "Phone Number of " + this.name;
                case 3:
                    return "Address of " + this.name;
                case 4:
                    return "Final";
            }
        },
    },
    components: {},
    mounted() {
        window.document.title = "Create User";
    },
});
