import { inspect } from "util";
import Vue from "vue";
export default Vue.extend({
    name: "create-user",
    data: () => {
        return {
            step: 1,
            monthModel: "",
            batch: "",
            batchMessage: "",
            members: [],
            memberModel: null,
            no_of_chits: null,
            users: [],
            loadedUsers: false,
            disableButtons: false,
            disableInputs: false,
            loading: false,
            submited: false,
            success: false,
            skipValidation: false,
            window: window,
        };
    },
    computed: {
        totalChits() {
            let total = 0;
            this.members.forEach((member) => {
                total += member.no_of_chits;
            });
            return total;
        },
        currentTitle() {
            switch (this.step) {
                case 1:
                    return "Create Group";
                case 2:
                    return "Batch Name";
                case 3:
                    return "Add members for " + this.batch + " batch";
                case 4:
                    return "Final";
            }
        },
        month() {
            return parseInt(this.monthModel.split("-")[1]);
        },
        year() {
            return parseInt(this.monthModel.split("-")[0]);
        },
        formatedMonth() {
            const date = new Date(this.monthModel);
            const month = [
                "January",
                "February",
                "March",
                "April",
                "May",
                "June",
                "July",
                "August",
                "September",
                "October",
                "November",
                "December",
            ];
            return month[date.getMonth()] + ", " + date.getFullYear();
        },
    },
    watch: {
        step: function () {
            window.resizeWindowToCard();
            if (this.step === 3)
                this.getUsers();
        },
    },
    methods: {
        empty() { },
        removeMember(member) {
            this.users.push(member.info);
            this.members.splice(this.members.indexOf(member), 1);
        },
        addMember() {
            if (this.memberModel && this.no_of_chits) {
                let removed = this.users.splice(this.users.indexOf(this.memberModel), 1);
                if (removed.length !== 1) {
                    this.users.push(...removed);
                    return;
                }
                this.members.push({
                    info: removed[0],
                    no_of_chits: parseFloat(this.no_of_chits),
                });
                this.memberModel = null;
                this.no_of_chits = null;
            }
        },
        next() {
            document.getElementById("next").click();
        },
        batchChange() {
            if (!this.batch)
                return;
            let bch = this.batch;
            bch = bch.toUpperCase().replace(/[^A-Z]/g, "");
            bch = bch.charAt(bch.length - 1);
            this.batchMessage = "";
            setTimeout(() => {
                this.batch = bch;
            }, 0);
        },
        stepForward() {
            if (this.skipValidation) {
                this.step++;
                return;
            }
            this.disableButtons = true;
            this.disableInputs = true;
            this.loading = true;
            this.validate((success) => {
                if (success)
                    this.step++;
                this.disableInputs = false;
                this.disableButtons = false;
                this.loading = false;
            });
        },
        getUsers() {
            if (this.loadedUsers)
                return;
            this.loading = true;
            this.disableInputs = true;
            window.ipcrenderer.once("get-users-data", (event, data) => {
                this.users = data;
                console.log(data);
                this.disableInputs = false;
                this.loading = false;
                this.loadedUsers = true;
            });
            window.ipcrenderer.send("get-users-data");
        },
        reloadUsers() {
            this.loading = true;
            this.disableInputs = true;
            window.ipcrenderer.once("get-users-data", (event, data) => {
                console.log(data);
                this.members.forEach((member) => {
                    this.users.splice(this.users.indexOf(member.info), 1);
                });
                this.disableInputs = false;
                this.loading = false;
            });
            window.ipcrenderer.send("get-users-data");
        },
